<script>
import { APIService } from "../../../apiservice.js";
export default {
  data() {
    return {
      showInput: false,
      showParagraph: false,
      user:{
        firstName:"",
        lastName:"",
        email:"",
        password:"",
        confirmPassword:"",
        referrer:"",
      },
      errMsg:"",
      refBy: false
    }
  },
  methods:{
  onSubmit() {
      let self = this;
      this.errMsg = "";
        if (
          this.user.firstName &&
          this.user.email &&
          this.user.password &&
          this.user.confirmPassword &&
          (!this.refBy || (this.user.referrer && this.refBy))
         ) {
           let data = {};
           if(!this.refBy){
             data = {
               firstName:this.user.firstName,
        lastName:this.user.lastName,
        email:this.user.email,
        password:this.user.password,
        confirmPassword:this.user.confirmPassword,
             }
           } else {
             let data = this.user;
           }
          APIService.signUp(data).then(
            res => {
              if (res.status == 200) {
              } else {
                self.errMsg = res.message;
                self.user.password = "";
                this.errors.clear();
                this.$validator.reset();
                // this.$refs.pass.$el.focus();
              }
            },
            err => {
              self.errMsg = "Something went wrong. Please try after sometime.";
            }
          );
        } else {
          this.errMsg = "Please check all required fields"
        }
    },
 signin() {
      this.$router.push({ name: "Login" });
    },
  }
};
</script>
